
* {
    box-sizing: border-box;
  }
  @media screen {

    html, body {
        min-height: 100%;
    }

    body {
        margin: 0;
        font-size: 18px;
        font-family: 'PT Sans', Verdana, sans-serif;
        background: #ffffff;
        line-height: 1.4;

        overflow: hidden;
    }

    /*** layout ***/
    .nav{
        position:fixed;
        z-index: 50;
        left:30px; top:20px;
        background-color: red;
        padding:30px
    }
    .flex-wrapper {
        display: flex;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        -webkit-flex-wrap: wrap;
        flex-wrap: wrap;
    }

    #page-wrap {
        width: 100%;
        height: 100vh;
        overflow: hidden;
    }

    /*** canvas ***/

    #canvas {
        height: 100vh;
        width: 100vw;
        z-index: 10;
    }

    



    #source-code-link:hover {
        background: black;
    }

    .multi-textures {
        position: absolute;
        top: 5%;
        right: 5%;
        bottom: 5%;
        left: 5%;
        z-index: 15;

        cursor: pointer;
        font-size: 3em;
        color: white;

        justify-content: center;
        align-items: center;
        align-content: center;

    }

    .multi-textures img {
        display: none;
        min-width: 100%;
        min-height: 100%;
        object-fit: cover;
    }

    /*** handling errors ***/

    .no-curtains .multi-textures {
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .no-curtains .multi-textures span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate3D(-50%, -50%, 0);
    }

    .no-curtains.image-1 .multi-textures img:nth-child(3) {
        display: block;
    }

    .no-curtains.image-2 .multi-textures img:nth-child(4) {
        display: block;
    }

    .no-curtains.image-3 .multi-textures img:nth-child(5) {
        display: block;
    }

    .no-curtains.image-4 .multi-textures img:nth-child(6) {
        display: block;
    }

}


@media screen and (max-width: 1000px) {

    .multi-textures {
        font-size: 2em;
    }

}